  var sepLen = separator.length;
  var valueLen = 5; // max possible length of element len("false")
  var estLen = (valueLen + sepLen) * lastIndex + valueLen;
  var result = changetype<string>(__new(estLen << 1, idof<string>()));
  var offset = 0;
  var value: bool;
