import * as glMatrix from './_common'
import { IndexedCollection } from './imports'
import { Maths } from './maths'
import { ReadonlyMat3 } from './_mat3'
import { ReadonlyMat4 } from './_mat4'
import { ReadonlyQuat } from './_quat'

export type vec3 = IndexedCollection;

export type ReadonlyVec3 = IndexedCollection;

/**
 * 3 Dimensional Vector
 * @module vec3
 */

/**
 * Creates a new, empty vec3
 *
 * @returns {vec3} a new 3D vector
 */
export function create(): vec3 {
	const out = new Float32Array(3)
	if (glMatrix.ARRAY_TYPE != glMatrix.ArrayTypeEnum.Float32ArrayT) {
		out[0] = 0
		out[1] = 0
		out[2] = 0
	}
	return out
}

/**
 * Creates a new vec3 initialized with values from an existing vector
 *
 * @param {ReadonlyVec3} a vector to clone
 * @returns {vec3} a new 3D vector
 */
export function clone(a: ReadonlyVec3): vec3 {
	const out = new Float32Array(3)
	out[0] = a[0]
	out[1] = a[1]
	out[2] = a[2]
	return out
}

/**
 * Calculates the length of a vec3
 *
 * @param {ReadonlyVec3} a vector to calculate length of
 * @returns {Number} length of a
 */
export function length(a: ReadonlyVec3): f32 {
	const x = a[0]
	const y = a[1]
	const z = a[2]
	return Maths.hypot3(x, y, z)
}

/**
 * Creates a new vec3 initialized with the given values
 *
 * @param {Number} x X component
 * @param {Number} y Y component
 * @param {Number} z Z component
 * @returns {vec3} a new 3D vector
 */
export function fromValues(x: f32, y: f32, z: f32): vec3 {
	const out = new Float32Array(3)
	out[0] = x
	out[1] = y
	out[2] = z
	return out
}

/**
 * Copy the values from one vec3 to another
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the source vector
 * @returns {vec3} out
 */
export function copy(out: vec3, a: ReadonlyVec3): vec3 {
	out[0] = a[0]
	out[1] = a[1]
	out[2] = a[2]
	return out
}

/**
 * Set the components of a vec3 to the given values
 *
 * @param {vec3} out the receiving vector
 * @param {Number} x X component
 * @param {Number} y Y component
 * @param {Number} z Z component
 * @returns {vec3} out
 */
export function set(out: vec3, x: f32, y: f32, z: f32): vec3 {
	out[0] = x
	out[1] = y
	out[2] = z
	return out
}

/**
 * Adds two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function add(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	out[0] = a[0] + b[0]
	out[1] = a[1] + b[1]
	out[2] = a[2] + b[2]
	return out
}

/**
 * Subtracts vector b from vector a
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function subtract(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	out[0] = a[0] - b[0]
	out[1] = a[1] - b[1]
	out[2] = a[2] - b[2]
	return out
}

/**
 * Multiplies two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function multiply(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	out[0] = a[0] * b[0]
	out[1] = a[1] * b[1]
	out[2] = a[2] * b[2]
	return out
}

/**
 * Divides two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function divide(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	out[0] = a[0] / b[0]
	out[1] = a[1] / b[1]
	out[2] = a[2] / b[2]
	return out
}

/**
 * Mathf.ceil the components of a vec3
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a vector to ceil
 * @returns {vec3} out
 */
export function ceil(out: vec3, a: ReadonlyVec3): vec3 {
	out[0] = Mathf.ceil(a[0])
	out[1] = Mathf.ceil(a[1])
	out[2] = Mathf.ceil(a[2])
	return out
}

/**
 * Mathf.floor the components of a vec3
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a vector to floor
 * @returns {vec3} out
 */
export function floor(out: vec3, a: ReadonlyVec3): vec3 {
	out[0] = Mathf.floor(a[0])
	out[1] = Mathf.floor(a[1])
	out[2] = Mathf.floor(a[2])
	return out
}

/**
 * Returns the minimum of two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function min(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	out[0] = Mathf.min(a[0], b[0])
	out[1] = Mathf.min(a[1], b[1])
	out[2] = Mathf.min(a[2], b[2])
	return out
}

/**
 * Returns the maximum of two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function max(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	out[0] = Mathf.max(a[0], b[0])
	out[1] = Mathf.max(a[1], b[1])
	out[2] = Mathf.max(a[2], b[2])
	return out
}

/**
 * Mathf.round the components of a vec3
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a vector to round
 * @returns {vec3} out
 */
export function round(out: vec3, a: ReadonlyVec3): vec3 {
	out[0] = Mathf.round(a[0])
	out[1] = Mathf.round(a[1])
	out[2] = Mathf.round(a[2])
	return out
}

/**
 * Scales a vec3 by a scalar number
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the vector to scale
 * @param {Number} b amount to scale the vector by
 * @returns {vec3} out
 */
export function scale(out: vec3, a: ReadonlyVec3, b: f32): vec3 {
	out[0] = a[0] * b
	out[1] = a[1] * b
	out[2] = a[2] * b
	return out
}

/**
 * Adds two vec3's after scaling the second operand by a scalar value
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @param {Number} scale the amount to scale b by before adding
 * @returns {vec3} out
 */
export function scaleAndAdd(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, scale: f32): vec3 {
	out[0] = a[0] + b[0] * scale
	out[1] = a[1] + b[1] * scale
	out[2] = a[2] + b[2] * scale
	return out
}

/**
 * Calculates the euclidian distance between two vec3's
 *
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {Number} distance between a and b
 */
export function distance(a: ReadonlyVec3, b: ReadonlyVec3): f32 {
	const x = b[0] - a[0]
	const y = b[1] - a[1]
	const z = b[2] - a[2]
	return Maths.hypot3(x, y, z)
}

/**
 * Calculates the squared euclidian distance between two vec3's
 *
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {Number} squared distance between a and b
 */
export function squaredDistance(a: ReadonlyVec3, b: ReadonlyVec3): f32 {
	const x = b[0] - a[0]
	const y = b[1] - a[1]
	const z = b[2] - a[2]
	return x * x + y * y + z * z
}

/**
 * Calculates the squared length of a vec3
 *
 * @param {ReadonlyVec3} a vector to calculate squared length of
 * @returns {Number} squared length of a
 */
export function squaredLength(a: ReadonlyVec3): f32 {
	const x = a[0]
	const y = a[1]
	const z = a[2]
	return x * x + y * y + z * z
}

/**
 * Negates the components of a vec3
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a vector to negate
 * @returns {vec3} out
 */
export function negate(out: vec3, a: ReadonlyVec3): vec3 {
	out[0] = -a[0]
	out[1] = -a[1]
	out[2] = -a[2]
	return out
}

/**
 * Returns the inverse of the components of a vec3
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a vector to invert
 * @returns {vec3} out
 */
export function inverse(out: vec3, a: ReadonlyVec3): vec3 {
	out[0] = 1.0 / a[0]
	out[1] = 1.0 / a[1]
	out[2] = 1.0 / a[2]
	return out
}

/**
 * Normalize a vec3
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a vector to normalize
 * @returns {vec3} out
 */
export function normalize(out: vec3, a: ReadonlyVec3): vec3 {
	const x = a[0]
	const y = a[1]
	const z = a[2]
	let len = x * x + y * y + z * z
	if (len > 0) {
		//TODO: evaluate use of glm_invsqrt here?
		len = 1 / Mathf.sqrt(len)
	}
	out[0] = a[0] * len
	out[1] = a[1] * len
	out[2] = a[2] * len
	return out
}

/**
 * Calculates the dot product of two vec3's
 *
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {Number} dot product of a and b
 */
export function dot(a: ReadonlyVec3, b: ReadonlyVec3): f32 {
	return a[0] * b[0] + a[1] * b[1] + a[2] * b[2]
}

/**
 * Computes the cross product of two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @returns {vec3} out
 */
export function cross(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3): vec3 {
	const ax = a[0],
		ay = a[1],
		az = a[2]
	const bx = b[0],
		by = b[1],
		bz = b[2]

	out[0] = ay * bz - az * by
	out[1] = az * bx - ax * bz
	out[2] = ax * by - ay * bx
	return out
}

/**
 * Performs a linear interpolation between two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @param {Number} t interpolation amount, in the range [0-1], between the two inputs
 * @returns {vec3} out
 */
export function lerp(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, t: f32): vec3 {
	const ax = a[0]
	const ay = a[1]
	const az = a[2]
	out[0] = ax + t * (b[0] - ax)
	out[1] = ay + t * (b[1] - ay)
	out[2] = az + t * (b[2] - az)
	return out
}

/**
 * Performs a spherical linear interpolation between two vec3's
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @param {Number} t interpolation amount, in the range [0-1], between the two inputs
 * @returns {vec3} out
 */
export function slerp(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, t: f32): vec3 {
	const angle = Mathf.acos(Mathf.min(Mathf.max(dot(a, b), -1), 1))
	const sinTotal = Mathf.sin(angle)

	const ratioA = Mathf.sin((1 - t) * angle) / sinTotal
	const ratioB = Mathf.sin(t * angle) / sinTotal
	out[0] = ratioA * a[0] + ratioB * b[0]
	out[1] = ratioA * a[1] + ratioB * b[1]
	out[2] = ratioA * a[2] + ratioB * b[2]

	return out
}

/**
 * Performs a hermite interpolation with two control points
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @param {ReadonlyVec3} c the third operand
 * @param {ReadonlyVec3} d the fourth operand
 * @param {Number} t interpolation amount, in the range [0-1], between the two inputs
 * @returns {vec3} out
 */
export function hermite(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, c: ReadonlyVec3, d: ReadonlyVec3, t: f32): vec3 {
	const factorTimes2 = t * t
	const factor1 = factorTimes2 * (2 * t - 3) + 1
	const factor2 = factorTimes2 * (t - 2) + t
	const factor3 = factorTimes2 * (t - 1)
	const factor4 = factorTimes2 * (3 - 2 * t)

	out[0] = a[0] * factor1 + b[0] * factor2 + c[0] * factor3 + d[0] * factor4
	out[1] = a[1] * factor1 + b[1] * factor2 + c[1] * factor3 + d[1] * factor4
	out[2] = a[2] * factor1 + b[2] * factor2 + c[2] * factor3 + d[2] * factor4

	return out
}

/**
 * Performs a bezier interpolation with two control points
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the first operand
 * @param {ReadonlyVec3} b the second operand
 * @param {ReadonlyVec3} c the third operand
 * @param {ReadonlyVec3} d the fourth operand
 * @param {Number} t interpolation amount, in the range [0-1], between the two inputs
 * @returns {vec3} out
 */
export function bezier(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, c: ReadonlyVec3, d: ReadonlyVec3, t: f32): vec3 {
	const inverseFactor = <f32>1 - t
	const inverseFactorTimesTwo = inverseFactor * inverseFactor
	const factorTimes2 = t * t
	const factor1 = inverseFactorTimesTwo * inverseFactor
	const factor2 = <f32>3 * t * inverseFactorTimesTwo
	const factor3 = <f32>3 * factorTimes2 * inverseFactor
	const factor4 = factorTimes2 * t

	out[0] = a[0] * factor1 + b[0] * factor2 + c[0] * factor3 + d[0] * factor4
	out[1] = a[1] * factor1 + b[1] * factor2 + c[1] * factor3 + d[1] * factor4
	out[2] = a[2] * factor1 + b[2] * factor2 + c[2] * factor3 + d[2] * factor4

	return out
}

/**
 * Generates a random vector with the given scale
 *
 * @param {vec3} out the receiving vector
 * @param {Number} [scale] Length of the resulting vector. If omitted, a unit vector will be returned
 * @returns {vec3} out
 */
export function random(out: vec3, scale: f32): vec3 {
	scale = scale || <f32>1.0

	const r = glMatrix.RANDOM() * <f32>2.0 * Mathf.PI
	const z = glMatrix.RANDOM() * <f32>2.0 - <f32>1.0
	const zScale = Mathf.sqrt(<f32>1.0 - z * z) * scale

	out[0] = Mathf.cos(r) * zScale
	out[1] = Mathf.sin(r) * zScale
	out[2] = z * scale
	return out
}

/**
 * Transforms the vec3 with a mat4.
 * 4th vector component is implicitly '1'
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the vector to transform
 * @param {ReadonlyMat4} m matrix to transform with
 * @returns {vec3} out
 */
export function transformMat4(out: vec3, a: ReadonlyVec3, m: ReadonlyMat4): vec3 {
	const x = a[0],
		y = a[1],
		z = a[2]
	let w = m[3] * x + m[7] * y + m[11] * z + m[15]
	w = w || 1.0
	out[0] = (m[0] * x + m[4] * y + m[8] * z + m[12]) / w
	out[1] = (m[1] * x + m[5] * y + m[9] * z + m[13]) / w
	out[2] = (m[2] * x + m[6] * y + m[10] * z + m[14]) / w
	return out
}

/**
 * Transforms the vec3 with a mat3.
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the vector to transform
 * @param {ReadonlyMat3} m the 3x3 matrix to transform with
 * @returns {vec3} out
 */
export function transformMat3(out: vec3, a: ReadonlyVec3, m: ReadonlyMat3): vec3 {
	const x = a[0],
		y = a[1],
		z = a[2]
	out[0] = x * m[0] + y * m[3] + z * m[6]
	out[1] = x * m[1] + y * m[4] + z * m[7]
	out[2] = x * m[2] + y * m[5] + z * m[8]
	return out
}

/**
 * Transforms the vec3 with a quat
 * Can also be used for dual quaternions. (Multiply it with the real part)
 *
 * @param {vec3} out the receiving vector
 * @param {ReadonlyVec3} a the vector to transform
 * @param {ReadonlyQuat} q quaternion to transform with
 * @returns {vec3} out
 */
export function transformQuat(out: vec3, a: ReadonlyVec3, q: ReadonlyQuat): vec3 {
	// benchmarks: https://jsperf.com/quaternion-transform-vec3-implementations-fixed
	const qx = q[0],
		qy = q[1],
		qz = q[2],
		qw = q[3]
	const x = a[0],
		y = a[1],
		z = a[2]
	// var qvec = [qx, qy, qz];
	// var uv = vec3.cross([], qvec, a);
	let uvx = qy * z - qz * y,
		uvy = qz * x - qx * z,
		uvz = qx * y - qy * x
	// var uuv = vec3.cross([], qvec, uv);
	let uuvx = qy * uvz - qz * uvy,
		uuvy = qz * uvx - qx * uvz,
		uuvz = qx * uvy - qy * uvx
	// vec3.scale(uv, uv, 2 * w);
	const w2 = qw * 2
	uvx *= w2
	uvy *= w2
	uvz *= w2
	// vec3.scale(uuv, uuv, 2);
	uuvx *= 2
	uuvy *= 2
	uuvz *= 2
	// return vec3.add(out, a, vec3.add(out, uv, uuv));
	out[0] = x + uvx + uuvx
	out[1] = y + uvy + uuvy
	out[2] = z + uvz + uuvz
	return out
}

/**
 * Rotate a 3D vector around the x-axis
 * @param {vec3} out The receiving vec3
 * @param {ReadonlyVec3} a The vec3 point to rotate
 * @param {ReadonlyVec3} b The origin of the rotation
 * @param {Number} rad The angle of rotation in radians
 * @returns {vec3} out
 */
export function rotateX(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, rad: f32): vec3 {
	const p: f32[] = [],
		r: f32[] = []
	//Translate point to the origin
	p[0] = a[0] - b[0]
	p[1] = a[1] - b[1]
	p[2] = a[2] - b[2]

	//perform rotation
	r[0] = p[0]
	r[1] = p[1] * Mathf.cos(rad) - p[2] * Mathf.sin(rad)
	r[2] = p[1] * Mathf.sin(rad) + p[2] * Mathf.cos(rad)

	//translate to correct position
	out[0] = r[0] + b[0]
	out[1] = r[1] + b[1]
	out[2] = r[2] + b[2]

	return out
}

/**
 * Rotate a 3D vector around the y-axis
 * @param {vec3} out The receiving vec3
 * @param {ReadonlyVec3} a The vec3 point to rotate
 * @param {ReadonlyVec3} b The origin of the rotation
 * @param {Number} rad The angle of rotation in radians
 * @returns {vec3} out
 */
export function rotateY(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, rad: f32): vec3 {
	const p: f32[] = [],
		r: f32[] = []
	//Translate point to the origin
	p[0] = a[0] - b[0]
	p[1] = a[1] - b[1]
	p[2] = a[2] - b[2]

	//perform rotation
	r[0] = p[2] * Mathf.sin(rad) + p[0] * Mathf.cos(rad)
	r[1] = p[1]
	r[2] = p[2] * Mathf.cos(rad) - p[0] * Mathf.sin(rad)

	//translate to correct position
	out[0] = r[0] + b[0]
	out[1] = r[1] + b[1]
	out[2] = r[2] + b[2]

	return out
}

/**
 * Rotate a 3D vector around the z-axis
 * @param {vec3} out The receiving vec3
 * @param {ReadonlyVec3} a The vec3 point to rotate
 * @param {ReadonlyVec3} b The origin of the rotation
 * @param {Number} rad The angle of rotation in radians
 * @returns {vec3} out
 */
export function rotateZ(out: vec3, a: ReadonlyVec3, b: ReadonlyVec3, rad: f32): vec3 {
	const p: f32[] = [],
		r: f32[] = []
	//Translate point to the origin
	p[0] = a[0] - b[0]
	p[1] = a[1] - b[1]
	p[2] = a[2] - b[2]

	//perform rotation
	r[0] = p[0] * Mathf.cos(rad) - p[1] * Mathf.sin(rad)
	r[1] = p[0] * Mathf.sin(rad) + p[1] * Mathf.cos(rad)
	r[2] = p[2]

	//translate to correct position
	out[0] = r[0] + b[0]
	out[1] = r[1] + b[1]
	out[2] = r[2] + b[2]

	return out
}

/**
 * Get the angle between two 3D vectors
 * @param {ReadonlyVec3} a The first operand
 * @param {ReadonlyVec3} b The second operand
 * @returns {Number} The angle in radians
 */
export function angle(a: ReadonlyVec3, b: ReadonlyVec3): f32 {
	const ax = a[0],
		ay = a[1],
		az = a[2],
		bx = b[0],
		by = b[1],
		bz = b[2],
		mag1 = Mathf.sqrt(ax * ax + ay * ay + az * az),
		mag2 = Mathf.sqrt(bx * bx + by * by + bz * bz),
		mag = mag1 * mag2,
		cosine = mag && dot(a, b) / mag
	return Mathf.acos(Mathf.min(Mathf.max(cosine, -1), 1))
}

/**
 * Set the components of a vec3 to zero
 *
 * @param {vec3} out the receiving vector
 * @returns {vec3} out
 */
export function zero(out: vec3): vec3 {
	out[0] = 0.0
	out[1] = 0.0
	out[2] = 0.0
	return out
}

/**
 * Returns a string representation of a vector
 *
 * @param {ReadonlyVec3} a vector to represent as a string
 * @returns {String} string representation of the vector
 */
export function str(a: ReadonlyVec3): string {
	return 'vec3(' + a[0].toString() + ', ' + a[1].toString() + ', ' + a[2].toString() + ')'
}

/**
 * Returns whether or not the vectors have exactly the same elements in the same position (when compared with ===)
 *
 * @param {ReadonlyVec3} a The first vector.
 * @param {ReadonlyVec3} b The second vector.
 * @returns {Boolean} True if the vectors are equal, false otherwise.
 */
export function exactEquals(a: ReadonlyVec3, b: ReadonlyVec3): bool {
	return a[0] === b[0] && a[1] === b[1] && a[2] === b[2]
}

/**
 * Returns whether or not the vectors have approximately the same elements in the same position.
 *
 * @param {ReadonlyVec3} a The first vector.
 * @param {ReadonlyVec3} b The second vector.
 * @returns {Boolean} True if the vectors are equal, false otherwise.
 */
export function equals(a: ReadonlyVec3, b: ReadonlyVec3): bool {
	const a0 = a[0],
		a1 = a[1],
		a2 = a[2]
	const b0 = b[0],
		b1 = b[1],
		b2 = b[2]
	return (
		Mathf.abs(a0 - b0) <=
      glMatrix.EPSILON * Maths.max(1.0, Mathf.abs(a0), Mathf.abs(b0)) &&
    Mathf.abs(a1 - b1) <=
      glMatrix.EPSILON * Maths.max(1.0, Mathf.abs(a1), Mathf.abs(b1)) &&
    Mathf.abs(a2 - b2) <=
      glMatrix.EPSILON * Maths.max(1.0, Mathf.abs(a2), Mathf.abs(b2))
	)
}

